import { ethers } from "ethers";
import { contracts, getAddress } from "../config/constants";

type Call = {
  name: string;
  address: string;
  params?: any[];
};

const getMulticall2Contract = (signerOrProvider: ethers.Signer | ethers.providers.Provider) => {
  return new ethers.Contract(getAddress(contracts.multicall2), contracts.multicall2.abi, signerOrProvider);
};

export const multicall = async (
  signerOrProvider: ethers.Signer | ethers.providers.Provider,
  abi: any,
  calls: Call[]
): Promise<any[][]> => {
  try {
    const multi = getMulticall2Contract(signerOrProvider);
    const itf = new ethers.utils.Interface(abi);

    const calldata = calls.map((call) => [call.address.toLowerCase(), itf.encodeFunctionData(call.name, call.params)]);
    const { returnData } = await multi.aggregate(calldata);

    const res = returnData.map((callResult: any, i: number) => itf.decodeFunctionResult(calls[i].name, callResult));

    return res;
  } catch (error) {
    throw new Error(error);
  }
};

export const multicall2 = async (
  signerOrProvider: ethers.Signer | ethers.providers.Provider,
  abi: any,
  calls: Call[],
  options = { requireSuccess: true }
): Promise<any[][]> => {
  const { requireSuccess } = options;
  const multi = getMulticall2Contract(signerOrProvider);
  const itf = new ethers.utils.Interface(abi);

  const calldata = calls.map((call) => [call.address.toLowerCase(), itf.encodeFunctionData(call.name, call.params)]);

  try {
    const returnData = await multi.tryAggregate(requireSuccess, calldata);

    const res = returnData.map((callResult: any, i: number) => {
      const [result, data] = callResult;
      return result ? itf.decodeFunctionResult(calls[i].name, data) : null;
    });
    return res;
  } catch (err) {
    console.log("[!] Multicall failed:", err);
    console.log("[!] calls:", calls);
    throw err;
  }
};
