import { ethers } from "ethers";
import { useContext, useEffect, useState } from "react";
import { Web3Context } from "../contexts/Web3Context";
import { UserContext } from "../contexts/UserContext";
import { A } from "../components/A";
import { addToken, onClickConnect, shortenAddress, switchChain } from "../util/misc";
import { CHAIN_ID, contracts, getAddress, REWARD_TOKEN_SYMBOL, SCAN } from "../config/constants";
import { tokens } from "../config/tokens";

import banner from "../resources/img/potluck-labs.png";

const f = (x: ethers.BigNumberish, units = 18): string => ethers.utils.formatUnits(x.toString(), units);
// const p = (x: string, units = 18): ethers.BigNumber => ethers.utils.parseUnits(x, units);
// const c = (x: number, d = 0) => ethers.utils.commify(x.toFixed(d));
const c = (x: string, d = 0) => ethers.utils.commify(parseFloat(x).toFixed(d));

export const Home = () => {
  const { account, provider, chainId } = useContext(Web3Context);
  const { pendingReward, ownedTokens, nfts, claim } = useContext(UserContext);

  const [loaded, setLoaded] = useState(false);

  const [needsUpdate, setNeedsUpdate] = useState(null);

  useEffect(() => {
    if (nfts == null) return;
    if (ownedTokens == null) return;

    for (const key in nfts) {
      const nft = nfts[key];
      const cs = ownedTokens[nft.address].checkpoints;
      for (const c of cs) {
        if (c === "0") {
          setNeedsUpdate(true);
          return;
        }
      }
    }
    setNeedsUpdate(false);
  }, [nfts, ownedTokens]);

  useEffect(() => {
    if (pendingReward == null) return;
    if (ownedTokens == null) return;
    if (nfts == null) return;
    setLoaded(true);
  }, [pendingReward, ownedTokens, nfts]);

  // if (!loaded) {
  //   return <div>Loading...</div>;
  // }

  const WrongNetwork = () => {
    return (
      <div>
        <div>You are connected to an unsupported chain.</div>
        <button className="btn" onClick={() => switchChain()}>
          Change Network
        </button>
      </div>
    );
  };

  const NotConnected = () => {
    return (
      <div>
        <button className="btn" onClick={() => onClickConnect(provider, account)}>
          Connect Wallet
        </button>
      </div>
    );
  };

  const Connected = () => {
    if (!loaded) {
      return <div>Loading...</div>;
    }
    return (
      <div>
        <button className="btn" onClick={() => claim()}>
          {needsUpdate && "Update &"} Claim
          <br />
          <span className="btn-amount">{c(f(pendingReward), 3)} $LABS</span>
        </button>

        <div>
          Connected as <A href={`${SCAN}/address/${account}`}>{shortenAddress(account, 4)}</A>
        </div>
        <br />
        <div>
          <span className="add-to-metamask" onClick={() => addToken(tokens[REWARD_TOKEN_SYMBOL])}>
            Add $LABS to MetaMask
          </span>
        </div>

        <div className="info">
          <h2>Approved NFT collections</h2>
          <p>
            Here you see which NFT collections you are able to earn rewards for, and which NFTs you own in each
            collection (by token ID).
          </p>
        </div>

        {needsUpdate && (
          <div className="have-untracked">
            You have untracked NFTs!
            <br />
            Press the "Update &amp; Claim" button to register them.
          </div>
        )}

        <div className="nfts">
          {Object.values(nfts).map((nft) => (
            <div className="nft" key={nft.address}>
              <div className="nft-name-symbol">
                {nft.name} ({nft.symbol})
              </div>

              <div className="nft-address">
                <A href={`${SCAN}/token/${nft.address}`}>{shortenAddress(nft.address, 4)}</A>
              </div>

              <div>Reward multiplier: {nfts[nft.address].weight}x</div>
              <br />

              <div>
                {ownedTokens[nft.address].tokenIds.map((x: any, i: number) => (
                  <div>
                    &nbsp; {ownedTokens[nft.address].checkpoints[i] === "0" ? "" : ""} Token {x}
                  </div>
                ))}
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  };

  return (
    <div className="home">
      <div className="wrapper">
        <img className="banner" src={banner} alt="Banner" />
        <div className="info">
          <h2>Hold NFTs, earn $LABS.</h2>
          <p>
            For every NFT you hold from a Potluck Labs-approved collection, you gain some $LABS (Potluck Labs Token)
            every second.
          </p>
          <p>
            In order to start earning rewards for an NFT, you need to register it with the{" "}
            <A href={`${SCAN}/address/${getAddress(contracts.rewarder)}`}>Rewarder contract</A> by pressing the button
            below.
          </p>
        </div>

        <div className="connection">
          {chainId !== CHAIN_ID ? <WrongNetwork /> : account ? <Connected /> : <NotConnected />}
        </div>

        <div className="boxes">
          <div className="box"></div>
          <div className="box"></div>
        </div>
      </div>
    </div>
  );
};
