import { ethers } from "ethers";
import { CHAIN_DATA, CHAIN_ID, getAddress } from "../config/constants";
import { T } from "../types/mytypes";

declare global {
  interface Window {
    ethereum: any;
  }
}

export const shortenAddress = (address: string, n = 4) => {
  return address.slice(0, 2 + n) + "..." + address.slice(-n);
};

export const toHex = (n: number) => {
  const x = `0x${n.toString(16)}`;
  console.log(`toHex(${n}) = ${x}`);
  return x;
};

export const switchOrAddChain = async () => {
  try {
    const chainId = await window.ethereum.request({ method: "eth_chainId" });
    console.log("chainId:", chainId);
    if (chainId === toHex(CHAIN_ID)) {
      return;
    }
  } catch (err) {
    console.error(err);
    throw err;
  }

  try {
    // check if the chain to connect to is installed
    await window.ethereum.request({
      method: "wallet_switchEthereumChain",
      params: [{ chainId: toHex(CHAIN_ID) }],
    });
  } catch (err) {
    // This error code indicates that the chain has not been added to
    // MetaMask. If it is not, then install it into the user MetaMask.
    if (err.code === 4902) {
      try {
        await window.ethereum.request({
          method: "wallet_addEthereumChain",
          params: [
            {
              chainId: toHex(CHAIN_ID),
              chainName: CHAIN_DATA.chainName,
              nativeCurrency: CHAIN_DATA.nativeCurrency,
              rpcUrls: CHAIN_DATA.rpcUrls,
              blockExplorerUrls: CHAIN_DATA.blockExplorerUrls,
            },
          ],
        });
      } catch (addError) {
        console.error(addError);
        throw addError;
      }
    }
    console.error(err);
    throw err;
  }
};

export const switchChain = async () => {
  if (!window.ethereum) {
    return alert("MetaMask is not installed. Please consider installing it: https://metamask.io/download.html");
  }

  try {
    await switchOrAddChain();
  } catch (err) {
    return alert(JSON.stringify(err));
  }
};

export const onClickConnect = async (provider: ethers.providers.Web3Provider, account: string) => {
  if (!window.ethereum) {
    return alert("MetaMask is not installed. Please consider installing it: https://metamask.io/download.html");
  }

  try {
    await switchOrAddChain();
  } catch (err) {
    return alert(JSON.stringify(err));
  }

  // setupMetaMaskEvents();

  console.log(account);

  if (account == null) {
    try {
      // Connecting sends an accountChanged event from MetaMask
      await provider.send("eth_requestAccounts", []);
    } catch (err) {
      return alert("Please connect to a MetaMask account.");
    }
  }
};

export const handleTxError = (err: any) => {
  console.error(err);
  if (err.code === 4001) {
    return; // User denied transaction, so just ignore
  }
  const extraMessage = err.data != null ? ` [${err.data.message}]` : "";
  return alert(`Error ${err.code}: ${err.message}${extraMessage}`);
};

export const addToken = async (token: T.Token) => {
  try {
    // Returns a boolean indicating whether or not the token was added.
    // Like any RPC method, an error may be thrown.
    await window.ethereum.request({
      method: "wallet_watchAsset",
      params: {
        type: "ERC20", // Initially only supports ERC20, but eventually more!
        options: {
          address: getAddress(token), // The address that the token is at.
          symbol: token.symbol, // A ticker symbol or shorthand, up to 5 chars.
          decimals: token.decimals, // The number of decimals in the token
          image: token.image, // A string url of the token logo
        },
      },
    });
  } catch (error) {
    console.log(error);
  }
};
