import { REWARD_TOKEN_ABI } from "./abi/rewardToken";
import { REWARDER_ABI } from "./abi/rewarder";
import { MULTICALL2_ABI } from "./abi/multicall2";
import { toHex } from "../util/misc";

export const FTM_MAIN = 250;
export const FTM_TEST = 4002;

export const REWARD_TOKEN_SYMBOL = "LABS";
export const REWARD_TOKEN_NAME = "Potluck Labs Token";
export const REWARD_TOKEN_DECIMALS = 18;
export const REWARD_TOKEN_URL = "";
const REWARD_TOKEN_ADDRESS_MAIN = "0x623d0f6a58e563C8627Da26B567269A18555aa52";
const REWARD_TOKEN_ADDRESS_TEST = "0x880B631ED94A265cA89a8E266513017033ffD222";

type Contract = {
  addresses: {
    [FTM_MAIN]: string;
    [FTM_TEST]: string;
  };
  abi: any;
};

type Contracts = {
  [REWARD_TOKEN_SYMBOL]: Contract;
  rewarder: Contract;
  multicall2: Contract;
};

export const contracts: Contracts = {
  [REWARD_TOKEN_SYMBOL]: {
    addresses: {
      [FTM_MAIN]: REWARD_TOKEN_ADDRESS_MAIN,
      [FTM_TEST]: REWARD_TOKEN_ADDRESS_TEST,
    },
    abi: REWARD_TOKEN_ABI,
  },

  rewarder: {
    addresses: {
      [FTM_MAIN]: "0xEd37405dE14e39f72e4A405DE32B6B90C96B97e5",
      [FTM_TEST]: "0xBaBFDD8cf3de860B8a78d3dadaBE2DCeB0F5394c",
    },
    abi: REWARDER_ABI,
  },

  multicall2: {
    addresses: {
      [FTM_MAIN]: "0x266557A864680A1401A3506c0eb72934BD13Bf59",
      [FTM_TEST]: "0x434fBefE0D506e2A6af3935AB80Ac9219c751413",
    },
    abi: MULTICALL2_ABI,
  },
};

type Config = {
  chainId: {
    [FTM_MAIN]: number;
    [FTM_TEST]: number;
  };
  rpcUrl: {
    [FTM_MAIN]: string;
    [FTM_TEST]: string;
  };
  scan: {
    [FTM_MAIN]: string;
    [FTM_TEST]: string;
  };
};

const config: Config = {
  chainId: {
    [FTM_MAIN]: FTM_MAIN,
    [FTM_TEST]: FTM_TEST,
  },
  rpcUrl: {
    [FTM_MAIN]: "https://rpc.ftm.tools",
    [FTM_TEST]: "https://rpc.testnet.fantom.network",
  },
  scan: {
    [FTM_MAIN]: "https://ftmscan.com",
    [FTM_TEST]: "https://testnet.ftmscan.com",
  },
};

const NETWORK = FTM_TEST;

export const RPC_URL = config.rpcUrl[NETWORK];
export const CHAIN_ID = config.chainId[NETWORK];
export const SCAN = config.scan[NETWORK];

const getChainData = (chainId: number): any => {
  switch (chainId) {
    case FTM_MAIN:
      return {
        chainId: toHex(FTM_MAIN),
        chainName: "Fantom",
        nativeCurrency: { name: "Fantom", symbol: "FTM", decimals: 18 },
        rpcUrls: [config.rpcUrl[FTM_MAIN]],
        blockExplorerUrls: [config.scan[FTM_MAIN]],
      };
    case FTM_TEST:
      return {
        chainId: toHex(FTM_TEST),
        chainName: "Fantom Testnet",
        nativeCurrency: { name: "Fantom", symbol: "FTM", decimals: 18 },
        rpcUrls: [config.rpcUrl[FTM_TEST]],
        blockExplorerUrls: [config.scan[FTM_TEST]],
      };
    default:
      return {
        chainId: toHex(chainId),
        chainName: undefined,
        nativeCurrency: undefined,
        rpcUrls: undefined,
        blockExplorerUrls: undefined,
      };
  }
};

export const CHAIN_DATA = getChainData(CHAIN_ID);

export const getAddress = (obj: {
  addresses: {
    [_: string]: string;
  };
}): string => {
  return obj.addresses[CHAIN_ID];
};
