import { createContext, ReactNode, useEffect, useState } from "react";

// const debug = (message: any, ...params: any[]) => {
//   console.log("[UtilContext]:", message, ...params);
// };

export const UtilContext = createContext<{
  medium: boolean;
  small: boolean;
}>({
  medium: null,
  small: null,
});

const mqlMedium = window.matchMedia(`(max-width: 700px)`);
const mqlSmall = window.matchMedia(`(max-width: 420px)`);

export const UtilContextProvider = ({ children }: { children: ReactNode }) => {
  const [medium, setMedium] = useState(mqlMedium.matches);
  const [small, setSmall] = useState(mqlSmall.matches);

  useEffect(() => {
    const mediaQueryChanged = () => {
      setMedium(mqlMedium.matches);
      setSmall(mqlSmall.matches);
    };
    mqlMedium.addEventListener("change", mediaQueryChanged);
    mqlSmall.addEventListener("change", mediaQueryChanged);
    return () => {
      mqlMedium.removeEventListener("change", mediaQueryChanged);
      mqlSmall.removeEventListener("change", mediaQueryChanged);
    };
  }, []);

  return (
    <UtilContext.Provider
      value={{
        medium: medium,
        small: small,
      }}
    >
      {children}
    </UtilContext.Provider>
  );
};
