import { Routes, Route } from "react-router-dom";

import { UtilContextProvider } from "./contexts/UtilContext";
import { AnyContextProvider } from "./contexts/AnyContext";
import { Web3ContextProvider } from "./contexts/Web3Context";
import { UserContextProvider } from "./contexts/UserContext";

import { Home } from "./views/Home";

function App() {
  return (
    <UtilContextProvider>
      <AnyContextProvider>
        <Web3ContextProvider>
          <UserContextProvider>
            <Routes>
              <Route path="/" element={<Home />} />
            </Routes>
          </UserContextProvider>
        </Web3ContextProvider>
      </AnyContextProvider>
    </UtilContextProvider>
  );
}
export default App;
