import { createContext, ReactNode, useCallback, useEffect, useState } from "react";
import { ethers } from "ethers";
import { contracts, getAddress, REWARD_TOKEN_SYMBOL, RPC_URL } from "../config/constants";
import { multicall2 } from "../util/multicall";

const debug = (message: any, ...params: any[]) => {
  // console.log("[AnyContext]:", message, ...params);
};

// Context that provides values readable without MetaMask
export const AnyContext = createContext<{
  anyProvider: ethers.providers.BaseProvider;

  luckyDecimals: number;
  luckyTotalSupply: string;
}>({
  anyProvider: null,

  luckyDecimals: null,
  luckyTotalSupply: null,
});

export const AnyContextProvider = ({ children }: { children: ReactNode }) => {
  const [provider, setProvider] = useState<ethers.providers.BaseProvider>(null);

  const [luckyDecimals, setLuckyDecimals] = useState<number>(null);
  const [luckyTotalSupply, setLuckyTotalSupply] = useState<string>(null);

  useEffect(() => {
    setProvider(ethers.providers.getDefaultProvider(RPC_URL));
  }, []);

  const loadLuckyData = useCallback(async () => {
    if (provider == null) return;
    debug("loadLuckyData");
    const luckyAddress = getAddress(contracts[REWARD_TOKEN_SYMBOL]);
    const luckyAbi = contracts[REWARD_TOKEN_SYMBOL].abi;
    const [
      [_decimals], //
      [_totalSupply],
    ] = await multicall2(provider, luckyAbi, [
      { address: luckyAddress, name: "decimals" },
      { address: luckyAddress, name: "totalSupply" },
    ]);
    setLuckyDecimals(_decimals);
    setLuckyTotalSupply(_totalSupply.toString());
  }, [provider]);

  const loadAll = useCallback(() => {
    loadLuckyData();
  }, [loadLuckyData]);

  useEffect(() => {
    loadAll();
    const interval = setInterval(loadAll, 5000);
    return () => clearInterval(interval);
  }, [loadAll]);

  return (
    <AnyContext.Provider
      value={{
        anyProvider: provider,

        luckyDecimals,
        luckyTotalSupply,
      }}
    >
      {children}
    </AnyContext.Provider>
  );
};
