const getBalances = async (addresses: string[], abi: any, provider: ethers.providers.Provider, account: string) => {
  if (provider == null) return;
  if (account == null) return;
  const calls = [];
  for (const address of addresses) {
    calls.push({
      address: address,
      name: "balanceOf",
      params: [account],
    });
  }
  const results = await multicall2(provider, abi, calls, { requireSuccess: true });
  const balances: Record<string, number> = {};
  for (let i = 0; i < addresses.length; i++) {
    const address = addresses[i];
    balances[address] = results[i][0].toString();
  }
  return balances;
};

const getOwnedTokenIds = async (
  balances: Record<string, number>,
  abi: any,
  provider: ethers.providers.Provider,
  account: string
) => {
  if (provider == null) return;
  if (account == null) return;
  const result: any = {};
  for (const nftAddress in balances) {
    const balance = balances[nftAddress];
    const calls1 = [];
    for (let i = 0; i < balance; i++) {
      calls1.push({
        address: nftAddress,
        name: "tokenOfOwnerByIndex",
        params: [account, i],
      });
    }
    const xx = await multicall2(provider, abi, calls1, { requireSuccess: true });
    const tokenIds = xx.map(([x]) => x.toString());

    const rewarderAddress = getAddress(contracts.rewarder);
    const calls2 = [];
    for (const tokenId of tokenIds) {
      calls2.push({
        address: rewarderAddress,
        name: "lastCheckpoint",
        params: [nftAddress, tokenId],
      });
    }
    const yy = await multicall2(provider, contracts.rewarder.abi, calls2, { requireSuccess: true });
    const checkpoints = yy.map(([y]) => y.toString());
    // debug("checkpoints:", checkpoints);

    result[nftAddress] = { tokenIds, checkpoints };
  }
  return result;
};

const getNfts = async (provider: ethers.providers.Provider, count: number) => {
  if (provider == null) return;

  const address = getAddress(contracts.rewarder);

  const calls1 = [];
  for (let i = 0; i < count; i++) {
    calls1.push({ address: address, name: "entries", params: [i] });
  }
  const results1 = await multicall2(provider, REWARDER_ABI, calls1, { requireSuccess: true });
  const rs1 = results1.map(([x, y]) => [x.toString(), y.toString()]);
  console.log(rs1);

  // TODO - name and symbol might fail!

  const calls2 = [];
  for (const [nftAddress] of rs1) {
    calls2.push({ address: nftAddress, name: "name", params: [] });
  }
  const results2 = await multicall2(provider, NFT_ABI, calls2, { requireSuccess: true });
  const rs2 = results2.map(([x]) => x.toString());

  const calls3 = [];
  for (const [nftAddress] of rs1) {
    calls3.push({ address: nftAddress, name: "symbol", params: [] });
  }
  const results3 = await multicall2(provider, NFT_ABI, calls3, { requireSuccess: true });
  const rs3 = results3.map(([x]) => x.toString());

  const xs: any = {};
  for (let i = 0; i < count; i++) {
    const [address, weight] = rs1[i];
    const name = rs2[i];
    const symbol = rs3[i];
    xs[address] = { address, weight, name, symbol };
  }

  return xs;
};

const debug = (message: any, ...params: any[]) => {
  console.log("[UserContext]:", message, ...params);
};

export const UserContext = createContext<{
  pendingReward: string;
  ownedTokens: any[];
  nfts: any[];
  claim: Function;
}>({
  pendingReward: null,
  ownedTokens: null,
  nfts: null,
  claim: null,
});

export const UserContextProvider = ({ children }: { children: ReactNode }) => {
  const { account, provider } = useContext(Web3Context);

  const [rewarderContract, setRewarderContract] = useState<ethers.Contract>(null);

  const [pendingReward, setPendingReward] = useState<string>(null);
  const [ownedTokens, setOwnedTokens] = useState<any>(null);
  const [nfts, setNfts] = useState<any[]>(null);

  // const [logs, setLogs] = useState<any[]>(null);

  useEffect(() => {
    if (account == null) return;
    if (provider == null) return;
    const signer = provider.getSigner();
    setRewarderContract(new ethers.Contract(getAddress(contracts.rewarder), contracts.rewarder.abi, signer));
  }, [account, provider]);

  const loadOwnedTokens = useCallback(async () => {
    if (account == null) return;
    if (provider == null) return;
    if (nfts == null) return;
    debug("loadOwnedTokens");

    const balances = await getBalances(Object.keys(nfts), NFT_ABI, provider, account);
    // debug("balances:", balances);

    const ownedTokenIds = await getOwnedTokenIds(balances, NFT_ABI, provider, account);
    // debug("ownedTokenIds:", ownedTokenIds);
    setOwnedTokens(ownedTokenIds);
  }, [account, provider, nfts]);

  const loadRewarder = useCallback(async () => {
    if (provider == null) return;
    if (rewarderContract == null) return;
    const count = (await rewarderContract.entriesCount()).toNumber();
    const nfts = await getNfts(provider, count);
    setNfts(nfts);
    setPendingReward((await rewarderContract.pendingReward()).toString());
  }, [provider, rewarderContract]);

  useEffect(() => {
    loadRewarder();
    const interval = setInterval(loadRewarder, 5000);
    return () => clearInterval(interval);
  }, [loadRewarder]);

  useEffect(() => {
    loadOwnedTokens();
    const interval = setInterval(loadOwnedTokens, 5000);
    return () => clearInterval(interval);
  }, [loadOwnedTokens]);

  const claim = useCallback(async () => {
    try {
      rewarderContract.update();
    } catch (err) {
      handleTxError(err);
    }
  }, [rewarderContract]);

  return (
    <UserContext.Provider
      value={{
        //   logs,
        pendingReward,
        ownedTokens,
        nfts,
        claim,
      }}
    >
      {children}
    </UserContext.Provider>
  );
};

